.header {
  width: 100%;
  padding-inline: 178px;
  position: absolute;
  z-index: 999;
  top: 0;
}
.header::after {
  position: absolute;
  bottom: 18px;
  content: "";
  background-color: hsla(0, 0%, 100%, 0.25);
  width: 81.5%;
  height: 1px;
}
@media (max-width: 992.99px) {
  .header::after {
    width: 100%;
    left: 0;
    bottom: 10px;
  }
}
@media (max-width: 600px) {
  .header::after {
    bottom: 8px;
  }
}
@media (max-width: 768px) {
  .header {
    padding-inline-start: 120px;
    padding-inline-end: 20px;
  }
}
@media (max-width: 400px) {
  .header {
    padding-inline-start: 90px;
    padding-inline-end: 10px;
  }
}

.nav {
  display: flex;
  justify-content: space-between;
  padding-block-start: 40px;
  padding-block-end: 48px;
}
@media (max-width: 768px) {
  .nav {
    padding-block: 30px;
  }
}
@media (max-width: 400px) {
  .nav {
    padding-block: 19px;
  }
}
.nav.hidden {
  display: none;
}

.number__body {
  display: flex;
  align-items: center;
  color: white;
  gap: 108px;
  font-family: "Roboto";
  letter-spacing: 10%;
}
@media (max-width: 1080px) {
  .number__body {
    gap: 80px;
  }
}

.burger__body {
  position: relative;
}
.burger__body:hover {
  transform: scale(0.9);
}

.burger__body span {
  position: absolute;
  width: 13px;
  height: 2px;
  background-color: white;
  top: 20px;
  left: 0;
}

.burger__body span::before,
.burger__body span::after {
  position: absolute;
  content: "";
  inset: 0;
  background-color: white;
  height: 2px;
}

.burger__body span::before {
  top: 8px;
  width: 30px;
}

.burger__body span::after {
  top: -8px;
  width: 25px;
}

.number {
  letter-spacing: 1.7px;
  margin-block-start: -13px;
  margin-inline-start: -1px;
}
@media (max-width: 992.99px) {
  .number {
    display: none;
  }
}

.request__body {
  display: flex;
  gap: 64px;
  align-items: center;
  padding-inline-end: 52px;
}
@media (max-width: 768px) {
  .request__body {
    padding-inline-end: 20px;
  }
}

.request__body-call {
  text-transform: uppercase;
  font-size: 16px;
  line-height: 100%;
  color: #ffac30;
  white-space: nowrap;
  font-family: "Open Sans";
  font-weight: 600;
  letter-spacing: 1.7px;
  margin-block-start: -8px;
}
.request__body-call:hover {
  transform: scale(0.9);
}
.request__body-call {
  cursor: pointer;
}
@media (max-width: 768px) {
  .request__body-call {
    margin-block-start: -9px;
  }
}
@media (max-width: 767px) {
  .request__body-call {
    display: none;
  }
}

.request__body-img {
  width: 12.5px;
  height: 12.5px;
}

.phone__body {
  position: relative;
  z-index: 2;
  padding: 14px;
  border-radius: 50px;
  margin-block-start: -10px;
}

.phone__rings {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  overflow: visible;
}

.phone__rings .ring-1,
.phone__rings .ring-2,
.phone__rings .ring-3 {
  fill: none;
  stroke: white;
  stroke-width: 3px;
}

.phone__rings .ring-1 {
  fill: white;
}

.phone__rings .ring-1 {
  opacity: 0.15;
}

.phone__rings .ring-2 {
  opacity: 0.3;
}

.phone__rings .ring-3 {
  opacity: 0.1;
}

@media (max-width: 400px) {
  .phone__rings--PC {
    display: none;
  }
}

.phone__rings--MOB {
  display: none;
}
@media (max-width: 400px) {
  .phone__rings--MOB {
    display: block;
  }
}